<template>
    <div class="faceInfo">
        <video id="video" class="video" height="482" autoplay="autoplay"></video>
    <div>
        <button class="capture" id="capture"></button>
    </div>
    <canvas id="canvas" class="canvas" height="482"></canvas>
    </div>
</template>
<script>
import { doFileUpload } from '@/api/user'
import html2canvas from 'html2canvas'
export default {
    name: "FaceInfo",
    data() {
        return {

        }
    },
    mounted () {
        let video = document.getElementById("video");
        if (navigator.mediaDevices === undefined) {
            navigator.mediaDevices = {};
        }

        if (navigator.mediaDevices.getUserMedia === undefined) {
            navigator.mediaDevices.getUserMedia = function (constraints) {

                let getUserMedia = navigator.webkitGetUserMedia || navigator.mozGetUserMedia;
                if (!getUserMedia) {
                    return Promise.reject(new Error('getUserMedia is not implemented in this browser'));
                }

                return new Promise(function (resolve, reject) {
                    getUserMedia.call(navigator, constraints, resolve, reject);
                });
            }
        }
        
        let constraints = {video: true};
        navigator.mediaDevices.getUserMedia(constraints)
            .then(function (stream) {

                if ("srcObject" in video) {

                    video.srcObject = stream;
                } else {

                    video.src = window.URL.createObjectURL(stream);
                }
                video.onloadedmetadata = function () {
                    video.play();
                };
            })
            .catch(function (err) {
                console.log(err.name + ": " + err.message);
            });

        this.$nextTick(() => {
            this.init()
        });
    },
    methods: {
        init() {
            let video = document.getElementById("video");
            let canvas = document.getElementById("canvas");
            let context = canvas.getContext("2d");
            document.getElementById("capture").addEventListener("click", () => {
                context.drawImage(video, 0, 0, screen.width, 320);
                html2canvas(canvas).then(resp => {
                    let base64Data = resp.toDataURL()
                    let fileImg = this.base64ToBlob(base64Data)
                    let formData = new FormData()
                    formData.append('files', fileImg, "faceImage.jpg")
                    doFileUpload(formData).then(resp => {
                        console.log(resp)
                        this.$router.push({name: "makeInvoice"})
                    })
                })
            });
        },
        base64ToBlob(urlData) {
            let arr = urlData.split(',');
            let mime = arr[0].match(/:(.*?);/)[1] || 'image/png';
            let bytes = window.atob(arr[1]);
            let ab = new ArrayBuffer(bytes.length);
            let ia = new Uint8Array(ab);

            for (let i = 0; i < bytes.length; i++) {
                ia[i] = bytes.charCodeAt(i);
            }

            return new Blob([ab], {
                type: mime
            });
        }
    },
}
</script>
<style lang="scss">
.video{
    width: 100%;
}
.canvas {
    width: 100%;
    border: 0;
}
.faceInfo{
    width: 642px;
    margin: 0 auto;
}
.capture{
    padding: 8px 28px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-shadow: 0 1px 4px #ccc;
    margin: 20px 0;
    background-color: #146aaf;
    color: #FFF;
    cursor: pointer;
    outline: none;
}
</style>